var boxes = require("./boxes");
var _p12Buffer, _certBuffer, _keyBuffer;

var KICA_DIALOG = {
    init: function() {
        // boxes define UI elements        
        var _boxes = boxes.boxes();

        // create secukit-wrapper div and append to the body element. 
        var SECUKIT_DIALOG = document.createElement("div");
        SECUKIT_DIALOG.id = "secukit-wrapper";
        SECUKIT_DIALOG.className = "d-none";
        SECUKIT_DIALOG.lang = webnpki_config.language.toLocaleLowerCase();
        SECUKIT_DIALOG.innerHTML = _boxes.main + _boxes.importCert + _boxes.extraDialog
        document.body.appendChild(SECUKIT_DIALOG);

        // enable draggable option.        
        $(".draggable-wrapper").draggable();

        // set button events
        _setEvents();

        // enable keyboard security
        if (webnpki_config.keyboardSecurity !== '')
            _enableKeyboardSecurity();
    },

    openSelectCertificateDialog: function() {
        _initDialog();
        _showCertList();
        $('#secukit-wrapper').removeClass('d-none');
    },

    closeSelectCertificateDialog: function() {
        _close();
    },

    selectCert: function(index) {
        $('#cert-list tr').removeClass('active');
        $('#cert-list tr[id=' + index + ']').addClass('active');
    },

    importBase64Cert: function(base64Cert) {
        KICA_DIALOG.openSelectCertificateDialog();
        $('#secukit-dialog').addClass('d-none');
        _showAskPwdDialog(true, base64Cert);
    }
}

function _close() {
    _destroyVK();
    $('#select-certificate-confirm').off('click');
    $('#secukit-wrapper').addClass('d-none');
}

function _closeFindCert() {
    $('#import-certificate-container').addClass('d-none');
}

function _initDialog() {
    var qlalfqjsgh = $('#qlalfqjsgh');
    qlalfqjsgh.val("");
    qlalfqjsgh.prop('disabled', false);
    qlalfqjsgh.prop('placeholder', KICA_CORPUS.CERT_PWD_PLACEHOLDER);

    webnpki.setVotmdnjem('qlalfqjsgh');
}

function _setCertInfoJson(certList) {
    function getDateFormat(d) {
        var yy = d.getFullYear();
        var mm = d.getMonth() + 1;
        var dd = d.getDate();
        return [yy, (mm > 9 ? '' : '0') + mm, (dd > 9 ? '' : '0') + dd].join('-');
    }

    function getIssuerName(id) {
        id = id.toLowerCase();
        var rslt = webnpki_config.issuers[id];

        return typeof rslt === "undefined" ? "unknown" : rslt;
    }

    var cnt = certList.length;

    var certInfoObj = {};
    certInfoObj.size = cnt;

    for (var i = 0; i < cnt; ++i) {
        var obj = {};
        var idx = (i + 1).toString();

        obj.cn = certList[i].getSubjectRDNs().CN;
        obj.username = certList[i].getSubjectRDNs().CN; // for cert Detail
        obj.index = idx;

        var tmp = certList[i].getIssuerRDNs().O;
        var issuerName = getIssuerName(tmp);

        obj.issuerToName = issuerName;
        obj.issuerToString = issuerName; // for cert Detail
        obj.oid = certList[i].getPolicyID();
        obj.policy = certList[i].getPolicyID(); // for cert Detail
        obj.todate = getDateFormat(certList[i].getNotAfter());
        obj.validateTo = getDateFormat(certList[i].getNotAfter()); // for cert Detail
        obj.validateFrom = getDateFormat(certList[i].getNotBefore()); // for cert Detail
        obj.usageToName = certList[i].getPolicyString();
        obj.policyToName = certList[i].getPolicyString(); // for cert Detail
        obj.userDN = certList[i].getSubjectDN();
        obj.issueDN = certList[i].getIssuerDN(); // for cert Detail
        obj.version = "Version " + certList[i]._fo.version; // for cert Detail
        obj.signatureAlgorithmID = certList[i].getSignAlgorithm(); // for cert Detail
        obj.keySize = certList[i].getKeySize(); // for cert Detail
        obj.certPem = certList[i].toPem();
        obj.certHex = certList[i].toHex();
        obj.certBase64 = certList[i].toBase64();
        obj.serial = certList[i]._fo.serialNumber;

        certInfoObj[idx] = obj;
    }

    return certInfoObj;
}

function _clearCertBuffer() {
    _certBuffer = undefined;
    _keyBuffer = undefined;
    _p12Buffer = undefined;
}

function _showCertList() {
    webnpki.getCertificateList(function(certificateList, error) {
        if (error) {
            KICA_ALERT.error(error.cdoe, error.message);
            return;
        }

        if (certificateList.length === 0) {
            $('tbody').children().remove();
            $('#dropzone-wrapper').removeClass('d-none');
            return;
        } else
            $('#dropzone-wrapper').addClass('d-none');

        var certificateListJSON = _setCertInfoJson(certificateList);

        var certificateListRoot = $('#cert-list');
        certificateListRoot.html("");

        for (var i = 0; i < certificateListJSON.size; i++) {
            var certificate = certificateListJSON[i + 1];

            var rowClass = "";
            if (i == 0)
                rowClass = 'class="active"';

            var index = i + 1;
            var row = $('<tr ' + rowClass + ' id=' + index + ' onclick="KICA_DIALOG.selectCert(' + index + ')">')

            var html = '<td scope="col"><span><i class="secukit-icon-cert"></i>' + certificate.usageToName + '</span></td>' +
                '<td scope="col"><span>' + certificate.cn + '</span></td>' +
                '<td scope="col"><span style="text-align: center;">' + certificate.todate + '</span></td>' +
                '<td scope="col"><span>' + certificate.issuerToName + '</span></td>';

            row.html(html);
            row.data('certificate', certificateList[i]);
            row.data('certificateJSON', certificate);

            certificateListRoot.append(row);
        }

        $("#cert-list-table").colResizable({ liveDrag: true });
    });
}

// Virtaul Keyborad
function _initVK(input) {
    var normal = [
        '` 1 2 3 4 5 6 7 8 9 0 - = {b}',
        'q w e r t y u i o p [ ] \\ ',
        'a s d f g h j k l ; \' {accept}',
        '{shift} z x c v b n m , . / {shift}',
    ];
    var shift = [
        '~ ! @ # $ % ^ & * ( ) _ + {b}',
        'Q W E R T Y U I O P { } |',
        'A S D F G H J K L : " {accept}',
        '{shift}  Z X C V B N M < > ? {shift}'
    ];

    var options = {
        // language defaults to ["en"] if not found
        rtl: false,

        // choose from 'qwerty', 'alpha', 'international', 'dvorak', 'num' or 'custom' (to use the customLayout below)
        layout: 'custom',
        customLayout: {
            'normal': normal,
            'shift': shift
        },
        // Used by jQuery UI position utility
        position: {
            of: null,
            my: 'center top',
            at: 'center top',
            at2: 'center bottom'
        },

        // allow jQuery position utility to reposition the keyboard on
        // window resize
        reposition: true,

        // true: preview added above keyboard;
        // false: original input/textarea used
        usePreview: false,

        // if true, the keyboard will always be visible
        alwaysOpen: false,

        // give the preview initial focus when the keyboard
        // becomes visible
        initialFocus: true,

        // Avoid focusing the input the keyboard is attached to
        noFocus: false,

        // if true, keyboard will remain open even if
        // the input loses focus.
        stayOpen: false,

        // Prevents the keyboard from closing when the user clicks or
        // presses outside the keyboard. The `autoAccept` option must
        // also be set to true when this option is true or changes are lost
        userClosed: false,

        // if true, keyboard will not close if you press escape.
        ignoreEsc: false,

        // *** change keyboard language & look ***
        display: {
            'meta1': '\u2666', // Diamond
            'meta2': '\u2665', // Heart

            // check mark (accept)
            'a': '\u2714:Accept (Shift-Enter)',
            'accept': 'Accept:Accept (Shift-Enter)',
            'alt': 'AltGr:Alternate Graphemes',
            // Left arrow (same as &larr;)
            'b': '\u2190:Backspace',
            'bksp': 'Backspace:Backspace',
            // big X, close/cancel
            'c': '\u2716:Cancel (Esc)',
            'cancel': 'Cancel:Cancel (Esc)',
            // clear num pad
            'clear': 'C:Clear',
            'combo': '\u00f6:Toggle Combo Keys',
            // num pad decimal '.' (US) & ',' (EU)
            'dec': '.:Decimal',
            // down, then left arrow - enter symbol
            'e': '\u21b5:Enter',
            'empty': '\u00a0', // &nbsp;
            'enter': 'Enter:Enter',
            // left arrow (move caret)
            'left': '\u2190',
            // caps lock
            'lock': '\u21ea Lock:Caps Lock',
            'next': 'Next \u21e8',
            'prev': '\u21e6 Prev',
            // right arrow (move caret)
            'right': '\u2192',
            // thick hollow up arrow
            's': '\u21e7:Shift',
            'shift': 'Shift:Shift',
            // +/- sign for num pad
            'sign': '\u00b1:Change Sign',
            'space': '\u00a0:Space',
            // right arrow to bar
            // \u21b9 is the true tab symbol
            't': '\u21e5:Tab',
            'tab': '\u21e5 Tab:Tab',
            // replaced by an image
            'toggle': ' ',

            // added to titles of keys
            // accept key status when acceptValid:true
            'valid': 'valid',
            'invalid': 'invalid',
            // combo key states
            'active': 'active',
            'disabled': 'disabled'

        },

        css: {
            // input & preview
            input: 'ui-widget-content ui-corner-all',
            // keyboard container
            container: 'ui-widget-content ui-widget ui-corner-all ui-helper-clearfix',
            // keyboard container extra class (same as container, but separate)
            popup: '',
            // default state
            buttonDefault: 'ui-state-default ui-corner-all',
            // hovered button
            buttonHover: 'ui-state-hover',
            // Action keys (e.g. Accept, Cancel, Tab, etc);
            // this replaces "actionClass" option
            buttonAction: 'ui-state-active',
            // Active keys
            // (e.g. shift down, meta keyset active, combo keys active)
            buttonActive: 'ui-state-active',
            // used when disabling the decimal button {dec}
            // when a decimal exists in the input area
            buttonDisabled: 'ui-state-disabled',
            // {empty} button class name
            buttonEmpty: 'ui-keyboard-empty'
        },

        // *** Useability ***
        // Auto-accept content when clicking outside the
        // keyboard (popup will close)
        autoAccept: false,
        // Auto-accept content even if the user presses escape
        // (only works if `autoAccept` is `true`)
        autoAcceptOnEsc: false,

        // Prevents direct input in the preview window when true
        lockInput: true,

        // mod key options: 'ctrlKey', 'shiftKey', 'altKey',
        // 'metaKey' (MAC only)
        // alt-enter to go to previous;
        // shift-alt-enter to accept & go to previous
        enterMod: 'altKey',

        // if true, the next button will stop on the last
        // keyboard input/textarea; prev button stops at first
        // if false, the next button will wrap to target the
        // first input/textarea; prev will go to the last
        stopAtEnd: true,

        // Set this to append the keyboard immediately after the
        // input/textarea it is attached to. This option works
        // best when the input container doesn't have a set width
        // and when the "tabNavigation" option is true
        appendLocally: false,
        // When appendLocally is false, the keyboard will be appended
        // to this object
        appendTo: '#secukit-wrapper',

        // If false, the shift key will remain active until the
        // next key is (mouse) clicked on; if true it will stay
        // active until pressed again
        stickyShift: true,

        // Prevent pasting content into the area
        preventPaste: false,

        // caret places at the end of any text
        caretToEnd: false,

        // caret stays this many pixels from the edge of the input
        // while scrolling left/right; use "c" or "center" to center
        // the caret while scrolling
        scrollAdjustment: 10,

        // Mouse repeat delay
        repeatDelay: 500,

        // Mouse repeat rate - after the repeatDelay
        repeatRate: 20,

        // resets the keyboard to the default keyset when visible
        resetDefault: false,

        // Event (namespaced) on the input to reveal the keyboard.
        // To disable it, just set it to ''.
        openOn: 'focus',

        // Event (namepaced) for when the character is added to the
        // input (clicking on the keyboard)
        keyBinding: 'mousedown touchstart',
    };

    if (webnpki_config.keyboardSecurity === "default") {
        options.beforeInsert = function(e, keyboard, el, txt) {
            if (txt.charCodeAt(0) === 8) {
                webnpki.modifyVotmdnjem(input.attr("id"), el.selectionStart, el.selectionEnd, 8, false);

                return txt;
            }
            webnpki.setVotmdnjem(input.attr("id"), el.selectionStart + 1, txt.charCodeAt(0));

            return String.fromCharCode(Math.floor(Math.random(1, 10) * 110) % 83 + 33);;
        };
        webnpki.setVotmdnjem(input.attr("id"));
    }

    input.keyboard(options);
    input.val('');
    input.focus();
}


function _closeVK() {
    ['#qlalfqjsgh', '#input-pfx-qlalfqjsgh'].forEach(function(id) {
        var vk = $(id).getkeyboard();
        if (typeof vk !== 'undefined' && vk.isOpen) { vk.close(); }
    });
}

function _destroyVK() {
    ['#qlalfqjsgh', '#input-pfx-qlalfqjsgh'].forEach(function(id) {
        var vk = $(id).getkeyboard();
        if (typeof vk !== 'undefined') {
            vk.destroy();
        }
    });
    $('#vkeyboard-toggle').removeClass('active');
}


function _download(href, fileName) {
    const a = document.createElement("a");
    a.style.display = "none";
    document.body.appendChild(a);

    // Set the HREF to a Blob representation of the data to be downloaded
    a.href = href;

    // Use download attribute to set set desired file name
    a.setAttribute("download", fileName);

    // Trigger the download by simulating click
    a.click();

    // Cleanup
    window.URL.revokeObjectURL(a.href);
    document.body.removeChild(a);
}

function _getOS() {
    var userAgent = window.navigator.userAgent,
        platform = window.navigator.platform,
        macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
        windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
        iosPlatforms = ['iPhone', 'iPad', 'iPod'],
        os = null;

    if (macosPlatforms.indexOf(platform) !== -1) {
        os = 'Mac OS';
    } else if (iosPlatforms.indexOf(platform) !== -1) {
        os = 'iOS';
    } else if (windowsPlatforms.indexOf(platform) !== -1) {
        os = 'Windows';
    } else if (/Android/.test(userAgent)) {
        os = 'Android';
    } else if (!os && /Linux/.test(platform)) {
        os = 'Linux';
    }

    return os;
}

function _enableKeyboardSecurity() {
    //   
    if (webnpki_config.keyboardSecurity === 'default') {
        var _votmdnjem = $('#qlalfqjsgh, #ask-qlalfqjsgh');
        _votmdnjem.off('keypress keydown');

        _votmdnjem.on('keypress', function(event) {
            event.preventDefault();
            var cursor = this.selectionStart;
            var temp = this.value;

            if (this.selectionStart !== this.selectionEnd) {
                temp = temp.substring(0, this.selectionStart) + temp.substring(this.selectionEnd);
                webnpki.modifyVotmdnjem(this.id, this.selectionStart, this.selectionEnd, 8, false);
            }

            this.value = temp + String.fromCharCode(Math.floor(Math.random(1, 10) * 110) % 83 + 33);

            if (this.setSelectionRange) {
                this.setSelectionRange(cursor + 1, cursor + 1);
            } else {
                var range = this.createTextRange();
                range.collapse(true);
                range.moveEnd('character', cursor + 1);
                range.moveStart('character', cursor + 1);
                range.select();
            }

            //                          
            webnpki.setVotmdnjem(this.id, this.selectionStart, event.which || event.keyCode);
        })

        _votmdnjem.on('keydown', function(event) {
            if (event.altKey || event.shiftKey || event.metaKey) {
                return;
            }

            var keycode = event.which || event.keyCode;
            if ((keycode === 46 || keycode === 8) && !$("#" + this.id).getkeyboard()) {
                webnpki.modifyVotmdnjem(this.id, this.selectionStart, this.selectionEnd, keycode, event.ctrlKey);
            }
        });
    }
}

function _setEvents() {

    // Browser Guide
    function _closeGuide() {
        $('#browser-guide-wrapper').addClass('d-none');
        $('#select-certificate-container').removeClass('open-guide');
    }

    $("#btn-open-browser-guide").on("click", function(event) {
        if ($('#browser-guide-wrapper').hasClass('d-none')) {
            $('#browser-guide-wrapper').removeClass('d-none');
            $('#select-certificate-container').addClass('open-guide');
        } else {
            _closeGuide();
        }
    })

    $("#btn-close-browser-guide").on("click", function(event) {
        _closeGuide();
    })

    $("#btn-open-detail-guide").on("click", function(event) {
        _download(webnpki_config.path + 'assets/guide/browser_certificate_guide.pdf', 'Browser Certificate Detail Guide.pdf');
    })

    // Drag and Drop Events
    function _onDragOverCert() {
        event.preventDefault();
        if (typeof event.target.classList !== 'undefined') {
            event.target.classList.add("dragover");
        }
    }

    function _onDragLeaveCert() {
        event.preventDefault();
        if (typeof event.target.classList !== 'undefined') {
            event.target.classList.remove("dragover");
        }
    }

    function _onDropCert(event) {
        event.preventDefault();
        event.target.classList.remove("dragover");
        if (!event.dataTransfer || event.dataTransfer.files.length == 0) {
            return;
        }
        _selectCert(event.dataTransfer);
    }

    document.getElementById("cert-list-wrapper").addEventListener('dragover', _onDragOverCert, false);
    document.getElementById("cert-list-wrapper").addEventListener('dragleave', _onDragLeaveCert, false);
    document.getElementById("cert-list-wrapper").addEventListener('drop', _onDropCert, false);

    // Delete Cert
    $("#btn-delete-cert").on("click", function(event) {
        var certificate = _getSelectedCertificate();

        if (!certificate) {
            KICA_ALERT.error("SELECT_CERT", KICA_ERROR_MSG.get("SELECT_CERT"))
            return;
        }

        KICA_ALERT.delcerAlert()
            .then(function(result) {
                //confirm
                if (result.value) {
                    webnpki.deleteCertificate(certificate)
                        .then(function(res, error) {
                            if (error == undefined)
                                KICA_DIALOG.openSelectCertificateDialog();
                            else
                                KICA_ALERT.error("DELETE_CERT_FAILED", KICA_ERROR_MSG.get("DELETE_CERT_FAILED"));
                        });
                }
            });
    })

    $(".vkeyboard-toggle").on("click", function(event) {
        var element = $(this);
        if (webnpki_config.keyboardSecurity === "default") {
            // Internal Password Reset
            webnpki.setVotmdnjem(element.prop("id"));
        }

        var input = $("#" + element.attr("data-target"));
        input.val("");
        var vk = input.getkeyboard();        

        if (typeof vk === 'undefined') {
            element.addClass('active');
            _initVK(input);
        } else {
            element.removeClass('active');
            vk.destroy();
        }
    });

    $("#qlalfqjsgh #ask-qlalfqjsgh").on("blur", function(event) {
        $(this).getkeyboard().close();
    });

    // Cancel Select Certificate
    $("#btn-select-cert-cancel").on("click", function(event) {
        _close();
    })

    // Open Find Cert
    $("#btn-find-cert").on("click", function(event) {
        $('#import-certificate-container').removeClass('d-none');
    })

    // Download Convert PFX     
    $("#btn-download-convert-pfx").on("click", function(event) {
        if (_getOS() === 'Windows') {
            _download(webnpki_config.path + 'assets/plugin/SecuKitCertExport.exe', 'SecuKitCertExport.exe');
        } else {
            KICA_ALERT.error("IS_NOT_WINDOWS", KICA_ERROR_MSG.get("IS_NOT_WINDOWS"));
        }
    })

    // Open Explorer
    $("#btn-open-explorer").on("click", function(event) {
        $('#input-cert').click();
    })

    // Close Find Cert
    $("#btn-close-find-cert").on("click", function(event) {
        _closeFindCert();
    })

    $("#input-cert").on("change", function(event) {
        event.preventDefault();
        _selectCert(this);
    })
}

function _selectCert(target) {
    var files = target.files;
    if (files.length === 0) { return; }

    function onloadReader(file, callback) {
        return function(event) {
            var cName = file.name.toLowerCase();
            if (/\.der$/.test(cName)) {
                _certBuffer = event.target.result;
                callback(false);
            } else if (/\.key$/.test(cName)) {
                _keyBuffer = event.target.result;
                callback(false);
            } else if (/\.pfx$|\.p12$/.test(cName)) {
                _p12Buffer = event.target.result;
                callback(true);
            }
        };
    }

    function fileLoadCallback(p12) {
        if (p12) {
            _showAskPwdDialog(p12);
        } else {
            var crt, key;
            crt = typeof _certBuffer === "undefined" ? false : true;
            key = typeof _keyBuffer === "undefined" ? false : true;
            if (crt && key) {
                webnpki_config.askPwd ? _showAskPwdDialog(p12) : _importCertAndKey();
            }
        }
    }

    var i, reader;
    for (i = 0; i < files.length; i += 1) {
        reader = new FileReader();
        reader.readAsArrayBuffer(files[i]);
        reader.onload = onloadReader(files[i], fileLoadCallback);
    }

    if (target.value) {
        target.value = "";
    }
}


function _importCertAndKey() {
    webnpki.importCertAndKey(_certBuffer, _keyBuffer).then(function() {
        _showCertList();
        $('#addiotnal-function-container').addClass('d-none');
        _closeFindCert();
        _clearCertBuffer();
        _closeVK();

    }).catch(function(error) {
        KICA_ALERT.error(error.code, error.message);
        _clearCertBuffer();
    });
}

function _showAskPwdDialog(p12, base64Cert) {
    function _askPwdOk(p12, base64Cert) {
        var pwd = $('#ask-qlalfqjsgh').val();
        if (pwd.length == 0) {
            KICA_ALERT.error("EMPTY_PASSWORD", KICA_ERROR_MSG.get("EMPTY_PASSWORD"), {
                onAfterClose: function() {
                    $('#ask-qlalfqjsgh').focus();
                }
            });

            return false;
        }

        function importError(e) {
            KICA_ALERT.error(e.code, e.message);

            if (webnpki_config.keyboardSecurity === 'default') {
                webnpki.setVotmdnjem('ask-qlalfqjsgh');
            }
            $('#ask-qlalfqjsgh').val('');
            $('#ask-qlalfqjsgh').focus();
        }

        if (p12) {
            if (base64Cert) {
                _p12Buffer = webnpki.bin.decodeBase64(base64Cert);
            }

            webnpki.importPKCS12(_p12Buffer, pwd).then(function() {
                _showCertList();
                $('#addiotnal-function-container').addClass('d-none');
                _closeFindCert();
                _clearCertBuffer();
                _closeVK();

                $('#ask-qlalfqjsgh').val('');
                if (webnpki_config.keyboardSecurity === 'default') {
                    webnpki.setVotmdnjem('ask-qlalfqjsgh');
                }

                if (base64Cert) {
                    KICA_ALERT.success("BASE64_IMPORT_CERT_SUCCESS", KICA_CORPUS.BASE64_IMPORT_CERT_SUCCESS);
                    KICA_DIALOG.closeSelectCertificateDialog();
                    $('#secukit-dialog').removeClass('d-none');
                }

            }).catch(function(e) {
                importError(e);
                return false;
            });
        } else {
            webnpki.importCertAndKey(_certBuffer, _keyBuffer, pwd).then(function() {
                _showCertList();
                $('#webnpki-wrapper').addClass('d-none');
                $('#input-pfx-qlalfqjsgh').val('');
                if (webnpki_config.keyboardSecurity === 'default') {
                    webnpki.setVotmdnjem('input-pfx-qlalfqjsgh');
                }
                _closeFindCert();
                _clearCertBuffer();
                _closeVK();

            }).catch(function(e) {
                console.log(e);
                importError(e);
                return false;
            });
        }

        return false;
    };


    function _askPwdCancel(base64Cert) {
        $('#addiotnal-function-container').addClass('d-none');
        _clearCertBuffer();
        _closeVK();
        $('#ask-qlalfqjsgh').val('');
        if (webnpki_config.keyboardSecurity === 'default') {
            webnpki.setVotmdnjem('ask-qlalfqjsgh');
        }

        if (base64Cert) {
            _closeFindCert();
            $('#secukit-dialog').removeClass('d-none');
            KICA_DIALOG.closeSelectCertificateDialog();
        }

        return false;
    };

    $('#addiotnal-function-container').removeClass('d-none');
    $('#ask-qlalfqjsgh').focus();

    $('#require-qlalfqjsgh-confirm').unbind('click');
    $('#require-qlalfqjsgh-confirm').click(function() { _askPwdOk(p12, base64Cert); });

    $('#require-qlalfqjsgh-cancel').unbind('click');
    $('#require-qlalfqjsgh-cancel').click(function() { _askPwdCancel(base64Cert); });
}


export default KICA_DIALOG;