import "./scss/dialog.scss";
import './vendor/jquery/jquery.keyboard';
import './vendor/jquery/colResizable-1.6';

import _KICA_DIALOG from './js/dialog';
import _KICA_ALERT from './js/alert';
import _KICA_LOGGER from './js/logger';

var _i18n = require("./lang/i18n"); 

(function() {
    window.KICA_DIALOG = _KICA_DIALOG;
    window.KICA_ALERT = _KICA_ALERT;
    window.KICA_LOGGER = _KICA_LOGGER;

    _i18n.setLanguage(webnpki_config.language, function(error) {
        if (error) {
            KICA_LOGGER.error(error);
            KICA_ALERT.error("LANGUAGE_NOT_SUPPORTED", error, { 'footer': 'Supported language codes are [ <b> KO, EN, and FR</b>].' });
            return;
        }
        
        KICA_DIALOG.init();
    });
})(); 